.header {
 background-color: #fff;
 border-bottom: 1px solid #dadce0;
 padding: 12px 24px;
 position: sticky;
 top: 0;
 z-index: 100;
 box-shadow: 0 2px 4px rgba(60, 64, 67, 0.15);
 backdrop-filter: blur(8px);
}
.header-content {
 max-width: 1200px;
 margin: 0 auto;
 display: flex;
 align-items: center;
 gap: 12px;
}
.header-logo {
 width: 40px;
 height: 40px;
 transition: transform 0.2s ease;
}
.header-logo:hover {
 transform: scale(1.05);
}
.header-title {
 font-size: 24px;
 font-weight: 500;
 color: #202124;
 letter-spacing: 0;
 margin: 0;
}

.theme-toggle {
 background: none;
 border: none;
 padding: 8px;
 border-radius: 50%;
 cursor: pointer;
 color: #5f6368;
 transition: all 0.2s ease;
 display: flex;
 align-items: center;
 justify-content: center;
 margin-left: auto;
}

.theme-toggle:hover {
 background-color: #f1f3f4;
 color: #202124;
}

/* Dark mode styles */
.app.dark-mode .header {
 background-color: #303134;
 border-bottom-color: #5f6368;
}

.app.dark-mode .header-title {
 color: #e8eaed;
}

.app.dark-mode .theme-toggle {
 color: #9aa0a6;
}

.app.dark-mode .theme-toggle:hover {
 background-color: #5f6368;
 color: #e8eaed;
}
